.main-section {
    background-image: url("./me.jpeg");
    background-repeat: no-repeat;
    background-size:100% 100%;
    text-align: center;
    height: 460px;
    padding: 5px;
    margin: 0px auto;
    color: aliceblue;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  .grid {
    display: flex;
    flex-wrap: wrap;
    max-width: 960px;
    width: 90%;
    margin: auto;
  }
  
  .cell {
    flex-basis: 33.3%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .cell:before {
    padding-bottom: 100%;
    display: block;
    content: '';
  }
  
  .circle {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin: 0 auto 1em;
    box-shadow: 0px 0px 15px 0px;
    overflow: hidden;
  }
  
  .circle img {
    width: 100%;
    position: relative;
    height: 100%;
  }

  .circle {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-position: center;
    background-size: cover;
    box-shadow: 0px 0px 15px 0px;
  }
  
  h3 {
    text-align: center;
  }